@media screen and (min-width: 700px) {
  #dialogue {
    
  }
}

#homeContainer {
  padding-top: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  height: 75vh;
}

#dialogue {
  display: flex;
  flex-direction: column;
  font-size: 6vh;
  text-align: center;
}

#homeButton {
  margin-top: auto;
  font-size: 9vh;
  cursor: pointer;
  transition: all 0.1s ease-out
}

#homeButton:hover {
  color: black
}

#codeLinkImage {
  width: 15vh;
  height: 15vh;
}

#codeLinkImage:hover {
  width: 16vh;
}

#codeLink {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: all 0.1s ease-out;
  text-align: center;
  margin: 0 auto;
}



